
.modal{
  width: 100%;
  max-width: 720px;
  background-color: #1c1c21;
  box-sizing: border-box;
  border: 1px solid rgba(76, 76, 255, .2);
  box-shadow: 0px 24px 32px rgba(0, 0, 0, 0.04), 
              0px 16px 24px rgba(0, 0, 0, 0.04), 
              0px 4px 8px rgba(0, 0, 0, 0.04), 
              0px 0px 1px rgba(0, 0, 0, 0.04);
  border-radius: 40px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 1000;
}

.closeIcon{
  position: absolute;
  top: 58px;
  right: 39px;
  cursor: pointer;
}

.orderDetails{
  height: 718px;
}

.ingredientDetails{
  min-height: 538px;
}

.orders{
  align-items: initial;
}