.order{
  width: 600px;
  height: calc(100vh - 88px);
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}

.orderStructure{
  width: 100%;
  flex-grow: 1;
  flex-shrink: 1;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  max-height: calc(100% - 144px);
}

.bun{
  flex-grow: 0;
  flex-shrink: 0;
}

.othersIngredients{
  width: 100%;
  max-height: calc(100% - 160px);
  padding-left: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  flex-shrink: 1;
  overflow-y: overlay;
}

.othersIngredients::-webkit-scrollbar {
  width: 8px;
}

.othersIngredients::-webkit-scrollbar-track {
  background: #2F2F37;
}

.othersIngredients::-webkit-scrollbar-thumb {
  background-color: #8585AD;
}

.otherIngredient{
  margin-top: 16px;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.otherIngredient:first-child{
  margin-top: 0;
}

.finishOrder{
  display: flex;
  justify-content: flex-end;
  flex-grow: 0;
  flex-shrink: 0;
}

.price{
  display: flex;
  align-items: center;
}

.canAccept{
  background-color: #3c3c44;
}