.main{
  height: calc(100vh - var(--header-height));
  width: 1240px;
  margin: 0 auto;
  display: flex;
}

.aside{
  width: 100%;
  max-width: 320px;
}

.menu{
  padding-left: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
}

.link{
  text-decoration: none;
  transition: var(--common-transition);
}

.linkActive{
  color: var(--text-primary-color);
}

.link:hover{
  color: var(--text-primary-color);
}

.content{
  width: calc(100% - 368px);
}